<template>
	<view class="listbox">
		<u-row>
			<u-col span="11"><view style="padding:10px">音频列表:</view></u-col>
			<u-col span="1"><u-icon name="trash" size="22" @click="clear"></u-icon></u-col>
		</u-row>
		<view v-for="(i, k) in audiolist" :key="k" class="list font_ellipsis" :class="[i.src === playinfo.src && 'list_item_bgColor']" @click="play(k)">
			{{ i.title }} - {{ i.singer }}
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				audiolist: this.$zaudio.audiolist, //当前音频列表
				playinfo: this.$zaudio.playinfo, //当前播放的信息
			}
		},
		methods: {
			play(key) {
				console.log(this.$zaudio.paused);
				this.paused = this.$zaudio.paused;
				//播放或暂停
				this.$zaudio.operate(key);
			},
			clear(){
				this.$nextTick(() => {
					this.audiolist = [];
					this.$zaudio.audiolist = [];
					this.$zaudio.stop();
				});
			},
		}
	}
</script>

<style lang="scss" scoped>
	.listbox {
		margin: 20px 10px 10px;
		border: 1px solid rgba(0, 0, 0, 0.2);
		.list {
			line-height: 40px;
			border-top: 1px solid #ccc;
			padding: 0 10px;
	
			button {
				float: right;
				margin-top: 5px;
				margin-left: 5px;
			}
		}
	}
	
	.font_ellipsis{
		overflow:hidden;
		white-space:nowrap; 
		text-overflow:ellipsis;
	}
	
	.list_item_bgColor{
		background-color: #ebebeb;
	}
</style>
